
import XHeader from 'vux/src/components/x-header/index.vue'

import Calendar from 'mpvue-calendar'
import 'mpvue-calendar/src/browser-style.css'
import {holiday} from '../libs/holiday'

const year = new Date().getFullYear();
const month = new Date().getMonth() + 1;
export default {
data () {
return {
value: [[year, month, 16], [year, month, 22]],
isrange: true,
weekSwitch: false,
ismulti: false,
dataInfo: {},
monFirst: true,
clean: false, //简洁模式
lunar: true, //显示农历
events: {'2019-2-7':'今日备注', '2019-2-8':'一条很长的明日备注'},
tileContent: holiday,
renderValues: [],
disabledArray: ['2019-1-27','2019-2-25'],
almanacs: {'11-14': '学生日', '11-22': '感恩日'}
}
},
components: {XHeader,Calendar},
mounted(){
this.handelRenderValues();
},
methods: {
switchMode(data){
this.weekSwitch = !this.weekSwitch;
setTimeout(() => {
this.$refs.calendar.renderer(2019, 1); 
}, 0)
},
handelRenderValues(data){
if (this.ismulti) {
this.renderValues = this.value.map(v => v.join('-'))
} else if (this.isrange) {
const values = [];
const valueData = data || this.value;
this.value.forEach((v, i) => {
values.push(v.join('-'));
if (!i) {
values.push('~');
}
});
this.renderValues = values;
} else {
this.renderValues = [this.value.join('-')];
}
},
multiMode(value){
this.ismulti = true;
this.isrange = false;
this.value = [[year, month, 16], [year, month, 18]];
this.handelRenderValues();
this.$refs.calendar.renderer(year, month);
},
rangeMode(value){
this.ismulti = false;
this.isrange = true;
this.value = [[year, month, 16], [year, month, 22]];
this.handelRenderValues();
this.$refs.calendar.renderer(year, month);
},
valueMode(value){
this.ismulti = false;
this.isrange = false;
this.value = [year, month, 16];
this.handelRenderValues();
this.$refs.calendar.renderer(year, month);
},
selectMonth(month,year){
console.log(year,month)
},
prev(y, m, w){
console.log(y, m, w)
},
next(year, month, week){
console.log(year, month, week)
},
selectYear(year){
console.log(year)
},
setToday() {
this.$refs.calendar.setToday();
},
dateInfo() {
const info = this.$refs.calendar.dateInfo(2018, 8, 23);
this.dataInfo = info;
console.log(info);
},
renderer() {
this.$refs.calendar.renderer(2018, 8); 
},
select(val, val2) {
if (this.isrange) {
this.handelRenderValues([val, val2]);
} else if (this.ismulti) {
this.handelRenderValues(val);
} else {
this.handelRenderValues([val]);
}
console.log(val);
console.log(val2);
}
}
}



